.app {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.app-header {
  background-color: #282c34;
  max-height: 15vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}

.profile-img {
  display: contents;
}

.app-links {
  display: flex;
  justify-content: space-evenly;
}

.app-links a {
  list-style: none;
  text-decoration: none;
  color: #2eb4da;
  padding: 2vw;
}

.app-links a :hover {
  color: #61dafb;
}

.app-body {
  background-color: #484d53;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.work-experience {
  justify-content: start;
  text-align: left;
  padding-left: 2in;
  padding-right: 2in;
}

.contact-links ul {
  display: inline-flex;
  list-style: none;
  padding-left: 0;
}

.contact-links a {
  color: azure;
  padding: 7px;
  text-decoration: none;
}

.contact-links a :hover {
  color: rgb(191, 191, 191);
}

.work-experience h4 {
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
}

.work-experience p {
  font-size: 13px;
  line-height: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}